import {useState, type FormEventHandler, useEffect} from "react";
import {clsx} from "keycloakify/tools/clsx";
import {useConstCallback} from "keycloakify/tools/useConstCallback";
import type {PageProps} from "keycloakify/login/pages/PageProps";
import {useGetClassName} from "keycloakify/login/lib/useGetClassName";
import type {KcContext} from "../kcContext";
import type {I18n} from "../i18n";
import {Input} from "components/ui/input";
import {Button, buttonVariants} from "../../components/ui/button";
import {Checkbox} from "../../components/ui/checkbox";
import {cn} from "../../lib/utils";
import {Label} from "../../components/ui/label";
import {Separator} from "../../components/ui/separator";
import {GitHubLogoIcon} from '@radix-ui/react-icons'
import {
    IconBrandBitbucket,
    IconBrandFacebook, IconBrandFacebookFilled, IconBrandGithub, IconBrandGitlab,
    IconBrandGoogle, IconBrandInstagram, IconBrandLinkedin, IconBrandOpenSource, IconBrandPaypal,
    IconBrandStackoverflow,
    IconBrandTwitter,
    IconBrandWindows,
    IconWindow
} from "@tabler/icons-react";
import {useTitle} from "react-use";

const my_custom_param = new URL(window.location.href).searchParams.get("my_custom_param");

if (my_custom_param !== null) {
    console.log("my_custom_param:", my_custom_param);
}
const prividerIdIconMap = {
    "microsoft": <IconBrandWindows/>,
    "google": <IconBrandGoogle/>,
    "facebook": <IconBrandFacebookFilled/>,
    "twitter": <IconBrandTwitter/>,
    "stackoverflow": <IconBrandStackoverflow/>,
    "gitlab": <IconBrandGitlab/>,
    "paypal": <IconBrandPaypal/>,
    "instagram": <IconBrandInstagram/>,
    "linkedin": <IconBrandLinkedin/>,
    "github": <IconBrandGithub/>,
    "bitbucket": <IconBrandBitbucket/>,
    "openshift": <IconBrandOpenSource/>
}

export default function Login(props: PageProps<Extract<KcContext, { pageId: "login.ftl" }>, I18n>) {
    const {kcContext, i18n, doUseDefaultCss, Template, classes} = props;
    const {getClassName} = useGetClassName({
        doUseDefaultCss,
        classes
    });

    const {
        social,
        realm,
        url,
        usernameHidden,
        login,
        auth,
        registrationDisabled,
        messagesPerField,
        recaptchaRequired,
        recaptchaSiteKey
    } = kcContext;

    const {msg, msgStr} = i18n;
    useTitle(i18n.msgStr("loginTitle", kcContext.realm.displayName))
    const [isLoginButtonDisabled, setIsLoginButtonDisabled] = useState(false);

    const onSubmit = useConstCallback<FormEventHandler<HTMLFormElement>>(e => {
        e.preventDefault();

        setIsLoginButtonDisabled(true);

        const formElement = e.target as HTMLFormElement;

        //NOTE: Even if we login with email Keycloak expect username and password in
        //the POST request.
        formElement.querySelector("input[name='email']")?.setAttribute("name", "username");

        formElement.submit();
    });
    useEffect(() => {
        if (recaptchaRequired && recaptchaSiteKey) {
            const recaptchaScriptUrl = kcContext.scripts?.[0] || "https://www.google.com/recaptcha/api.js"; // fallback for Storybook

            let script = document.querySelector(`script[src="${recaptchaScriptUrl}"]`) as HTMLScriptElement | null;

            const renderRecaptcha = () => {
                if (window.grecaptcha && document.getElementById("recaptcha-container")) {
                    try {
                        window.grecaptcha.render("#recaptcha-container", {
                            sitekey: recaptchaSiteKey
                            // other reCAPTCHA options as needed
                        });
                    } catch (e) {

                    }

                }

            };

            if (!script) {
                script = document.createElement("script");
                script.src = recaptchaScriptUrl;
                script.async = true;
                document.head.appendChild(script);

                script.onload = () => {
                    setTimeout(renderRecaptcha, 1000);
                };
            } else {
                setTimeout(renderRecaptcha, 1000);
            }

            return () => {
                if (document.body.contains(script)) {
                    document.body.removeChild(script!);
                }
            };
        }
    }, [recaptchaRequired, recaptchaSiteKey, kcContext.scripts]);
    return (
        <Template
            {...{kcContext, i18n, doUseDefaultCss, classes}}
            displayInfo={social.displayInfo}
            displayWide={realm.password && social.providers !== undefined}
            headerNode={msg("doLogIn")}
            infoNode={
                realm.password &&
                realm.registrationAllowed &&
                !registrationDisabled && (
                    <div id="kc-registration" className={"justify-center items-center text-center mb-3 w-full"}>
                        <Separator className={"mb-1 mt-1"}/>
                        <span className={"text-slate-500 text-sm"}>
                            {msg("noAccount")} {' '}
                            <a tabIndex={6} href={url.registrationUrl} className={"text-slate-950"}>
                                {msg("doRegister")}
                            </a>
                        </span>
                    </div>
                )
            }
        >

            {/*<div id="kc-form" className={clsx(realm.password && social.providers !== undefined && getClassName("kcContentWrapperClass"))}>*/}
            {/*    <div*/}
            <div className="flex gap-4">
                <div className={`mx-auto flex w-full flex-col justify-center`}
                     id="kc-form-wrapper"
                    // className={clsx(
                    //     realm.password &&
                    //     social.providers && [getClassName("kcFormSocialAccountContentClass"), getClassName("kcFormSocialAccountClass")]
                    // )}
                >
                    {realm.password && (
                        <form id="kc-form-login" onSubmit={onSubmit} action={url.loginAction} method="post"
                              className={"flex flex-col gap-3 items-center w-full min-w-[350px]"}>
                            <div className={"w-full flex flex-col items-center justify-center"}>
                                {!usernameHidden &&
                                    (() => {
                                        const label = !realm.loginWithEmailAllowed
                                            ? "username"
                                            : realm.registrationEmailAsUsername
                                                ? "email"
                                                : "usernameOrEmail";

                                        const autoCompleteHelper: typeof label = label === "usernameOrEmail" ? "username" : label;

                                        return (
                                            <div className="grid w-full max-w-sm items-center gap-1.5">
                                                <Label htmlFor="autoCompleteHelper"> {msg(label)}</Label>
                                                <Input
                                                    tabIndex={1}
                                                    id={autoCompleteHelper}
                                                    className={getClassName("kcInputClass")}
                                                    name={autoCompleteHelper}
                                                    defaultValue={login.username ?? ""}
                                                    type="text"
                                                    autoFocus={true}
                                                    autoComplete="off"
                                                />

                                            </div>
                                        );
                                    })()}
                            </div>
                            <div className={"w-full flex flex-col items-center justify-center"}>
                                <div className="grid w-full max-w-sm items-center gap-1.5">
                                    <Label htmlFor="password">{msg("password")}</Label>
                                    <Input
                                        tabIndex={2}
                                        id="password"
                                        name="password"
                                        type="password"
                                        autoComplete="off"
                                    />
                                </div>
                                {realm.resetPasswordAllowed && (
                                    <span
                                        className={"text-sm mt-1 w-full text-slate-500 w-full text-center items-start flex justify-start"}>
                                            <a tabIndex={5} href={url.loginResetCredentialsUrl}>
                                                {msg("doForgotPassword")}
                                            </a>
                                        </span>
                                )}
                            </div>
                            <div className={"w-full flex flex-col items-center justify-center"}>
                                <div id="kc-form-options">
                                    {realm.rememberMe && !usernameHidden && (
                                        <div className="flex items-center space-x-2">
                                            <Checkbox id="terms2" defaultChecked={login.rememberMe === "on"}/>
                                            <label
                                                htmlFor="terms2"
                                                className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
                                            >
                                                {msg("rememberMe")}
                                            </label>
                                        </div>

                                    )}
                                </div>

                            </div>

                            <input
                                type="hidden"
                                id="id-hidden-input"
                                name="credentialId"
                                {...(auth?.selectedCredential !== undefined
                                    ? {
                                        "value": auth.selectedCredential
                                    }
                                    : {})}
                            />
                            {recaptchaRequired && (
                                <div className="form-group">
                                    <div className={getClassName("kcInputWrapperClass")}>
                                        <div className="g-recaptcha w-full first:w-full" data-size="normal"
                                             id="recaptcha-container" data-sitekey={recaptchaSiteKey}/>
                                    </div>
                                </div>
                            )}
                            <Button
                                tabIndex={4}
                                name="login"
                                id="kc-login"
                                type="submit"
                                className={"min-w-[200px]"}
                                disabled={isLoginButtonDisabled}
                            >
                                {msgStr("doLogIn")}
                            </Button>
                        </form>
                    )}

                </div>
                {realm.password && social.providers !== undefined && (
                    <div
                        id="kc-social-providers"
                        className={'flex gap-4 w-auto'}
                    >
                        <Separator orientation="vertical"/>
                        <div
                            className={"grid flex-col flex-wrap grid-cols-2 gap-2 w-[320px]"}
                        >
                            {social.providers.map(p => (
                                <Button onClick={() => window.location.href = p.loginUrl} id={`zocial-${p.alias}`}
                                        key={p.providerId}
                                        className={`${buttonVariants({variant: "secondary"})} w-[160px] flex justify-start gap-2`}>
                                    {/*@ts-ignore*/}
                                    {prividerIdIconMap[p.providerId]}
                                    <span>{p.displayName}</span>
                                </Button>
                            ))}
                        </div>
                    </div>
                )}
            </div>
        </Template>
    );
}
